.usersList {
  /* width: 70%; */
  height: 450px;
  margin-left: 100px;
  text-align: left;
  overflow-y: scroll;
  border: 2px solid black;
}

/* table{
  cellspacing: 10px;
} */

table {
  border-spacing: 0 1rem;
  /* border: 2px solid black; */
}

th {
  /* position: relative; */
  position: sticky;
}

th,
td {
  padding: 0px 20px 10px 10px;
}

tr {
  /* display: inline-flex; */
  outline: thin solid;
  margin-top: 30px;
  cursor: pointer;
  border: 2px solid black;
}

tr:hover {
  box-shadow: -1px 3px 24px -3px rgba(0, 0, 0, 0.71);
}
